<template>
  <div class="certificate">
    <van-nav-bar
      title="提交资质"
      left-text="返回"
      fixed
      left-arrow
      @click-left="$toPage(-1)"
    />
    <van-cell-group>
      <van-field
        v-model="bdTel"
        size="large"
        label="商务经理"
        maxlength="11"
        placeholder="如果没有对应的商务经理，请不用填写"
        :error-message="message"
        @focus="message = ''"
        @input="blurBd"
      />
    </van-cell-group>
    <p class="reg_tip">
      {{ $t("messages.regist.tip") }}
    </p>
    <van-tabs type="card" animated @click="tabClick">
      <van-tab title="医疗单位">
        <ul class="reg_license">
          <li v-for="(reg, index) in reg_info" :key="index" v-if="reg.isShow">
            <div
              v-show="!reg.url"
              class="reg_src"
              @click="reg_img_select(index)"
            />
            <img
              v-show="reg.url"
              :src="reg.url"
              @click.stop.prevent="imgChange(index)"
            />
            {{ reg.name }}
          </li>
        </ul>
      </van-tab>
      <van-tab title="药品经营企业">
        <ul class="reg_license">
          <li v-for="(reg, index) in reg_info" :key="index" v-if="reg.isShow">
            <div
              v-show="!reg.url"
              class="reg_src"
              @click="reg_img_select(index)"
            />
            <img
              v-show="reg.url"
              :src="reg.url"
              @click.stop.prevent="imgChange(index)"
            />
            {{ reg.name }}
          </li>
        </ul>
      </van-tab>
    </van-tabs>

    <van-popup v-model="show">
      <p @click="viewImg(uploadIndex)">
        {{ $t("messages.regist.lookImg") }}
      </p>
      <p @click="reg_img_select(uploadIndex)">
        {{ $t("messages.regist.changeImg") }}
      </p>
    </van-popup>
    <van-image-preview v-model="mask" :images="showImg" />

    <van-button type="info" size="normal" block @click="fun_btn_regFinish">
      {{ $t("messages.regist.reg_btn2") }}
    </van-button>
    <div v-show="loading" class="loading">
      <div>
        <van-loading size="36px" vertical type="spinner" color="#1989fa" />
        <p>文件上传中...</p>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      reg_info: [
        {
          id: 10,
          isShow: true,
          fileList: [],
          url: "",
          name: "营业执照",
          message: "请上传营业执照"
        },
        {
          id: 11,
          isShow: false,
          fileList: [],
          url: "",
          name: "药店经营许可证",
          message: "请上传药店经营许可证"
        },
        {
          id: 12,
          isShow: false,
          fileList: [],
          url: "",
          name: "GSP认证",
          message: "请上传GSP认证"
        },
        {
          id: 15,
          isShow: true,
          fileList: [],
          url: "",
          name: "医疗机构执业许可证",
          message: "请上传医疗机构执业许可证"
        },
        {
          id: 13,
          isShow: true,
          fileList: [],
          url: "",
          name: "采购提货人委托书",
          message: "请上传采购提货人委托书"
        },
        {
          id: 14,
          isShow: true,
          fileList: [],
          url: "",
          name: "采购提货人身份证",
          message: "请上传采购提货人身份证"
        },
        {
          id: 16,
          isShow: true,
          fileList: [],
          url: "",
          name: "诊疗科目副本",
          message: "请上传诊疗科目副本"
        }
      ],
      show: false,
      uploadIndex: 0,
      mask: false,
      showImg: ["https://img.yzcdn.cn/vant/cat.jpeg"],
      loading: false,
      bdTel: "",
      type: 0, //  单位类型
      compid: 0,
      cityCode: 0,
      userPhone: 0,
      message: ''
    };
  },
  computed: {
    userInfo() {
      return this.$loadUserInfo();
    }
  },
  mounted() {
    this.compid = this.$route.query.compid;
    this.cityCode = this.$route.query.cityCode;
    this.userPhone = this.$route.query.userPhone;
  },
  methods: {
    //  完成注册
    fun_btn_regFinish() {
      JNB.query({
        serverName: "userServer",
        cls: "StoreManageModule",
        method: "updateStoreCustomer",
        params: {
          uphone: this.bdTel,
          compid: this.compid,
          areac: this.cityCode,
          storetype: this.type
        },
        callback: data => {
          let result = JSON.parse(data);
          if (result.code === 200) {
            if(!this.bdTel){
              this.bindUnqBd()
            }
            this.onloadIMG();
          } else {
            this.$toast.fail(result.message);
          }
        }
      });
    },
    bindUnqBd() {
      JNB.query({
        serverName: "userServer",
        cls: "BDManageModule",
        method: "bindUnqBd",
        params: {
          areac: this.cityCode,
          compid: this.compid,
          uphone: this.userPhone
        },
        callback: data => {
          let result = JSON.parse(data);
        }
      });
    },
    onloadIMG() {
      JNB._requestNative(
        JNB._transfer("globalServer", "FileInfoModule", "fileServerInfo"),
        JSON.stringify({ compid: this.compid }),
        data => {
          const list = [];
          const o = JSON.parse(data);
          this.reg_info.forEach(element => {
            if (element.url.slice(0, 5) === "image") {
              list.push({
                remotePath: o.data.companyFilePath,
                fileName: element.id + ".jpg",
                uri: element.url
              });
            }
          });
          if (list.length > 0) {
            this.loading = true;
            JNB._requestNative(
              "fileUpload",
              JSON.stringify({ url: o.data.upUrl, files: list }),
              json => {
                const data = JSON.parse(json);
                if (data.code === 200) {
                  this.loading = false;
                  this.$toast.success("注册成功！");
                  this.$router.push({
                    name: "login"
                  });
                } else {
                  this.$toast("注册失败！");
                }
              }
            );
          } else {
            this.$toast.success("注册成功,请联系相关BD上传资质证书！");
            this.$router.push({
              name: "login"
            });
          }
        }
      );
    },
    //  选择证书
    reg_img_select(index) {
      this.show = false;
      JNB._requestNative("openImageSelect", url => {
        if (url) {
          this.reg_info[index].url = url;
        }
      });
    },
    //  更换证书
    imgChange(index) {
      // 选择图片
      this.show = true;
      this.uploadIndex = index;
    },
    //  查看大图
    viewImg(uploadIndex) {
      this.show = false;
      this.mask = true;
      this.showImg[0] = this.reg_info[uploadIndex].url;
    },
    //  切换tab
    tabClick(index) {
      if (index === 1) {
        this.reg_info[1].isShow = true;
        this.reg_info[2].isShow = true;
        this.reg_info[3].isShow = false;
        this.reg_info[6].isShow = false;
        this.type = 1;
      } else {
        this.reg_info[1].isShow = false;
        this.reg_info[2].isShow = false;
        this.reg_info[3].isShow = true;
        this.reg_info[6].isShow = true;
        this.type = 0;
      }
    },
    blurBd() {
      JNB.query({
        serverName: "userServer",
        cls: "StoreManageModule",
        method: "existStoreCustomer",
        params: {
          uphone: this.bdTel,
          compid: this.compid,
          areac: this.cityCode
        },
        callback: data => {
          let result = JSON.parse(data);
          if(!result.data){
            this.message = result.message
          }else{
            this.message = ''
          }
        }
      });
    }
  }
};
</script>
<style scoped>
.certificate {
  background-color: #fff;
  padding-top: 86px;
  box-sizing: border-box;
  height: 100vh;
}
.certificate .reg_license {
  margin-top: 20px;
}
.certificate .reg_license li {
  width: 344px;
  height: 343px;
  border: 1px solid rgba(230, 230, 230, 1);
  line-height: 343px;
  text-align: center;
  font-size: 16px;
  display: inline-block;
  margin: 18px 0 0 18px;
  position: relative;
  background: url("..\..\assets\reg_icon\add.png") no-repeat;
  background-size: 70%;
  background-position: center center;
}
.certificate .reg_license li img,
.certificate .reg_license li .reg_src {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
}
.certificate .van-cell-group .van-field {
  padding: 20px 30px;
}
.certificate .reg_tip {
  padding: 10px 30px;
  font-size: 14px;
  margin: 0;
  color: rgba(102, 102, 102, 1);
}
.certificate .van-button {
  width: 540px;
  height: 88px;
  background: rgba(237, 47, 38, 1);
  border-radius: 8px;
  border: none;
  margin: 30px 105px;
  /* position: absolute;
  bottom: 30px;
  left: 105px; */
}
.certificate .van-popup {
  width: 300px;
  padding: 0 50px;
}
.certificate .van-popup p {
  padding: 10px;
  user-select: none;
}
.certificate .loading {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  background-color: rgba(0, 0, 0, 0.2);
  z-index: 99;
}
.certificate .loading div {
  margin: auto;
  font-size: 36px;
}
.certificate .van-tabs {
  margin-top: 30px;
  z-index: 0;
}
</style>
